import { Webhook as SvixWebhook } from "svix";
import { WebhookVerifyOptions } from "@/interfaces";

export const verifyWebhookSignature = async (options: WebhookVerifyOptions) => {
  const svixWebhook = new SvixWebhook(options.webhook_secret);
  try {
    await svixWebhook.verify(options.payload, options.headers);
  } catch (err) {
    throw new Error("Invalid webhook signature");
  }
};
